.footer {
	position: fixed;
	bottom: 0;
	height: 15%;
}

.footer > p {
	padding: 0;
}

.star {
	text-align: center;
	position: absolute;
	bottom: 0;
	z-index: -1;
	overflow: hidden;
	animation-name: riseAndDissapear;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
